html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

#demo-container {
  border: 3px solid #333;
  margin-bottom: 15px;
  padding: 20px;
}

#demo-container > :first-child {
  margin-top: 0;
}

#demo-container > :last-child {
  margin-bottom: 0;
}

form {
  margin-bottom: 5px;
}

.original, .new {
  border: 3px dashed #333;
  padding: 10px;
}

.new.replace-with {
  background-color: #ff00001a;
  border-color: red;
}

.new.update-with {
  background-color: #0080001a;
  border-color: green;
}

.new.prepend-with {
  background-color: #0000ff1a;
  border-color: #00f;
}

.new.append-with {
  background-color: #ffd7001a;
  border-color: gold;
}

.new.before-with {
  background-color: #0080801a;
  border-color: teal;
}

.new.after-with {
  background-color: #8000801a;
  border-color: purple;
}

